// 引入框架、组件
import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import routeConfig from './router/router.config'
import Vuex from 'vuex'
// element-ui
import {
    Tag,
    Menu,
    Input,
    Table,
    Dialog,
    Button,
    Select,
    Popover,
    Tooltip,
    Carousel,
    MenuItem,
    Pagination,
    TableColumn,
    CarouselItem,
    Autocomplete
} from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

// import stylesheet
import './assets/css/code.scss'
import './assets/css/public.scss'
import './assets/css/blog.scss'

// 引入基本信息
import baseInfo from './assets/data/baseInfo.data'
// 引入标签信息
import allTags from './assets/data/tags.data'
// 引入原始 blog 简介数据
import initialBlogData from './assets/data/blog.data'

Vue.use(Tag)
Vue.use(Menu)
Vue.use(Input)
Vue.use(Table)
Vue.use(Dialog)
Vue.use(Button)
Vue.use(Select)
Vue.use(Popover)
Vue.use(Tooltip)
Vue.use(MenuItem)
Vue.use(Carousel)
Vue.use(Pagination)
Vue.use(TableColumn)
Vue.use(CarouselItem)
Vue.use(Autocomplete)
// 引入自定义的回到顶部自定义组件
const GoTop = () => import('./components/GoTop.vue')

// 公共头部
const Header = () => import('./components/Header.vue')

// 公共尾部
const Bottom = () => import('./components/Bottom.vue')

// 个人信息
const Info = () => import('./components/Info.vue')
// Blog 列表自定义组件
const Blog = () => import('./components/Blog.vue')

Vue.use(VueRouter)
Vue.use(Vuex)

// 注册自定义组件
Vue.component('cl-header', Header)
Vue.component('cl-go-top', GoTop)
Vue.component('cl-bottom', Bottom)
Vue.component('cl-blog', Blog)
Vue.component('cl-info', Info)

// 定义路由
const router = new VueRouter({
    // mode:'history',
    routes: routeConfig
})
router.beforeEach((to, from, next) => {
    if (to.matched.length === 0) {
        from.name ? next({ name: from.name }) : next('/')
    } else {
        next()
    }
})

const arrayMethod = {
    copyIndex: (array, itemNumber) => {
        let current = []
        array.forEach((e, i) => {
            if (i <= itemNumber - 1) current.push(e)
            else return false
        })
        return current
    },
    copyAll: array => {
        let current = []
        array.forEach((e, i) => {
            current.push(e)
        })
        return current
    }
}

// 初始化搜索数据
const searchBlogData = arrayMethod.copyAll(initialBlogData)
const currentBlogData = arrayMethod.copyIndex(initialBlogData, 10)
const formatSearch = key => {
    return key.toLowerCase().replace(/\s/g, '')
}

/* 获取 当前页 数据 函数
*  @param initial 初始数据 （一般传入 searchBlogData）
*  @param state   $store 存储的 state 里的数据
*  @param obj     传入对象  格式： {currentPage:2, type:1 } // 当前页码，类型：0  blog 1 demo。暂无 demo
* */
const getThisPageData = (initial, state, obj) => {
    const start = (obj.currentPage - 1) * state.pageSize - 1
    const end = obj.currentPage * state.pageSize - 1
    let current = []
    initial.forEach((e, i) => {
        if (i > start && i <= end) {
            current.push(initial[i])
        }
    })
    return current
}

// vuex 保存数据
const store = new Vuex.Store({
    state: {
        owner: baseInfo.owner,
        motto: baseInfo.motto,
        pageSize: 10,
        btnOpacity: 0,
        menuOpacity: 1,
        isScroll: false,
        tags: allTags,
        searchVis: true,
        currentBlogData: currentBlogData,
        searchBlogData: searchBlogData,
        initialBlogData: initialBlogData,
        totalBlogLength: initialBlogData.length
    },
    mutations: {
        // 普通搜索
        search (state, key) {
            state.currentBlogData = []
            state.searchBlogData = []
            state.pageSize = 10
            state.initialBlogData.forEach((e, i) => {
                if (
                    formatSearch(e.brief).includes(key) ||
                    formatSearch(e.title).includes(key)
                ) {
                    state.searchBlogData.push(state.initialBlogData[i])
                    if (state.currentBlogData.length < state.pageSize) {
                        state.currentBlogData.push(state.initialBlogData[i])
                    }
                }
            })
            state.totalBlogLength = state.searchBlogData.length
        },
        // 初始化搜索条件
        initialSearch (state) {
            let _arr = []
            for (let i = 0; i <= 9; i++) {
                _arr.push(state.initialBlogData[i])
            }
            state.currentBlogData = _arr
            state.searchBlogData = state.initialBlogData
            state.totalBlogLength = state.initialBlogData.length
            state.pageSize = state.currentBlogData.length
        },
        // 按类型搜索
        searchForType (state, name) {
            let arr = []
            state.searchBlogData.forEach((element, index) => {
                element.tags.forEach((e, i) => {
                    if (name === e.name) {
                        arr.push(element)
                    }
                })
            })
            state.currentBlogData = arr
            state.totalBlogLength = arr.length
            state.pageSize = arr.length
        },
        // PC端 换一页
        pageChange (state, obj) {
            // 判断类型 1 demo , 0 blog
            if (obj.type === 0) {
                state.currentBlogData = getThisPageData(
                    state.searchBlogData,
                    state,
                    obj
                )
                state.totalBlogLength = state.searchBlogData.length
            }
            document.documentElement.scrollTop = document.body.scrollTop = 0
        },
        // 手机端 加载更多（加入一页数据）
        loadNextPage (state, obj) {
            let nextPageData = []
            // 判断类型 1 demo , 0 blog
            if (obj.type === 0) {
                nextPageData = getThisPageData(state.searchBlogData, state, obj)
                nextPageData.forEach((e, i) => {
                    state.currentBlogData.push(e)
                })
            }
        }
    }
})

const vm = new Vue({
    router,
    store,
    el: '#App',
    render: h => h(App)
})

Vue.use(vm)



