<template>
    <main id="App">
        <!-- 头部 -->
        <cl-header ref="header"></cl-header>
        <!-- 主体内容 -->
        <main class="container">
            <transition name="el-zoom-in-left">
                <router-view></router-view>
            </transition>
        </main>
        <!-- 尾部 -->
        <cl-bottom ref="bottom"></cl-bottom>
        <!-- 回到顶部，自定义组件 -->
        <cl-go-top ref="goTop"></cl-go-top>
        <!-- 移动端搜索，自定义组件 -->
        <!--<my-search :searchShow="searchVis"></my-search>-->
    </main>
</template>
<script>
export default {
  name: 'app'
};
</script>



